import React, { Component } from "react";
// import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Form,
  Icon,
  Input,
  DatePicker,
  Row,
  Col,
  Checkbox,
  Select,
  Radio
} from "antd";
import ModalAddJobTitle from "./modalAddJobTitle";
import ModalAddDepartment from "./modalAddDepartment";
import getLanguage from "../../../App/MyLanguange";

const { Option } = Select;
// const { TextArea } = Input;
const InputGroup = Input.Group;

class EmployeeCreateProfilForm extends Component {
  state = {
    check_code: false,
    is_taxable: 0,
    id_location: [],
    id_location_error: null
  };

  handleChangeWorkLocation = e => {
    //console.log(e);
    if (this.state.id_location_error !== null) {
      this.setState({
        id_location_error: null
      });
    }

    if (e.includes("all")) {
      // console.log("光");
      this.setState({
        id_location: this.props.employee.array_id_location
      });
    } else {
      if (e.length === 0) {
        this.setState({
          id_location_error: <IntlMessages id="required" />
        });
      }
      // console.log("闇");
      this.setState({
        id_location: e
      });
    }
  };

  handleChangeCode = () => {
    const { setFieldsValue } = this.props.form;
    this.setState({ check_code: !this.state.check_code });
    if (this.state.check_code === false) {
      setFieldsValue({
        ["employee_code"]: null
      });
    }
  };

  handleSubmit = e => {
    //console.log("Masuk Pak")
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      let arr = this.state.id_location;
      if (arr.length === 0) {
        this.setState({
          id_location_error: <IntlMessages id="required" />
        });
      } else if (!err) {
        values.id_location = this.state.id_location;
        // console.log(values)
        this.props.submit(values);
      }
    });
  };

  handleChangeTaxable = e => {
    //const {setFieldsValue} = this.props.form
    //console.log(value);
    this.setState({ is_taxable: e.target.value });
    // setFieldsValue({
    //   ["tax_number"]: null,
    // })
  };

  render() {
    // console.log(this.state.id_location);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { employee } = this.props;
    const { loading, loadingSubmit } = employee;
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    // const data = employee.employee ? employee.employee : [];

    // const used = data.is_used;
    const error = employee.errorField ? employee.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const radioGroupStyle = {
      display: "flex",
      width: "50%"
    };

    const radioButtonStyle = {
      flex: "1",
      textAlign: "center"
    };

    return (
      <>
        <ModalAddJobTitle
          handleModal={this.props.handleModalAddJobTitle}
          jobTitle={this.props.jobTitle}
          submit={values => this.props.submitJobTitle(values)}
          visible={this.props.visibleAddJobTitle}
        ></ModalAddJobTitle>
        <ModalAddDepartment
          department={this.props.department}
          handleModal={this.props.handleModalAddDepartment}
          submit={values => this.props.submitDepartment(values)}
          visible={this.props.visibleAddDepartment}
        ></ModalAddDepartment>
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          {/* {used ? (
        <Row style={{ marginBottom: "20px" }}>
          <Col span={24}>
            <Alert
              message={<IntlMessages id="employee.help" />}
              type="warning"
              showIcon
            />
          </Col>
        </Row>
      ) : (
        ""
      )} */}
          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 15px 0 0" }}
            >
              <InputGroup compact>
                <Col span={20}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="employees.add.code" />}
                    validateStatus={
                      error.employee_code || getFieldError("employee_code")
                        ? "error"
                        : null
                    }
                    help={
                      error.employee_code
                        ? error.employee_code
                        : getFieldError("employee_code")
                    }
                  >
                    {getFieldDecorator("employee_code", {
                      // initialValue: data.employee_code,
                      rules: [
                        {
                          max: 30,
                          message: <IntlMessages id="maxLength30" />
                        }
                      ]
                    })(
                      <Input disabled={this.state.check_code ? true : false} />
                    )}
                  </Form.Item>
                </Col>
                <Col span={4}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="autoGenerateCode" />}
                  >
                    <Checkbox
                      style={{
                        paddingTop: "5px",
                        paddingLeft: "8px"
                      }}
                      checked={this.state.check_code}
                      onChange={this.handleChangeCode}
                    />
                  </Form.Item>
                </Col>
              </InputGroup>

              <InputGroup>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="employees.add.firstName" />}
                  >
                    {getFieldDecorator("first_name", {
                      // initialValue: data.employee_name,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        },
                        {
                          max: 50,
                          message: <IntlMessages id="maxLength50" />
                        }
                      ]
                    })(
                      <Input
                        //   disabled={used ? true : false}
                        style={{ width: "100%" }}
                      />
                    )}
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="employees.add.lastName" />}
                  >
                    {getFieldDecorator("last_name", {
                      // initialValue: data.employee_name,
                      rules: [
                        {
                          max: 50,
                          message: <IntlMessages id="maxLength50" />
                        }
                      ]
                    })(
                      <Input
                        //   disabled={used ? true : false}
                        style={{ width: "100%" }}
                      />
                    )}
                  </Form.Item>
                </Col>
              </InputGroup>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="employees.filter.registeredAt" />}
              >
                {getFieldDecorator("id_registered_location", {
                  //initialValue: 0,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={this.props.employee.loading}
                    showSearch
                    optionFilterProp="children"
                    //disabled={used ? true : false}
                  >
                    {this.props.employee.locations.map((data, key) => (
                      <Option key={key} value={data.id_location}>
                        {data.location_name}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="jobTitles.pageHeader" />}
              >
                {getFieldDecorator("id_job_title", {
                  //initialValue: 0,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={this.props.employee.loading}
                    showSearch
                    optionFilterProp="children"
                    //disabled={used ? true : false}
                  >
                    {permissions.includes("Create Job Title") ? (
                      <Option
                        key={"Create Job Title"}
                        value={undefined}
                        onMouseDown={e => e.preventDefault()}
                        onClick={this.props.handleModalAddJobTitle}
                      >
                        <div style={{ color: "#1890FF", fontStyle: "italic" }}>
                          {" + " +
                            getLanguage({
                              id: "jobTitles.pageHeader"
                            })}
                        </div>
                      </Option>
                    ) : null}
                    {this.props.employee.job_titles.map((data, key) => (
                      <Option key={key} value={data.id_job_title}>
                        {data.job_title_name}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="employees.filter.locations" />}
                required
                validateStatus={this.state.id_location_error ? "error" : null}
                help={this.state.id_location_error}
              >
                <Select
                  mode="multiple"
                  loading={this.props.employee.loading}
                  showSearch
                  allowClear={true}
                  optionFilterProp="children"
                  onChange={this.handleChangeWorkLocation.bind(this)}
                  value={this.state.id_location}
                  //disabled={used ? true : false}
                >
                  <Option key={10} value={"all"}>
                    {" "}
                    {<IntlMessages id="select.all.location" />}
                  </Option>
                  {this.props.employee.locations.map((data, key) => (
                    <Option key={key} value={data.id_location}>
                      {data.location_name}
                    </Option>
                  ))}
                </Select>
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="employees.filter.departments" />}
              >
                {getFieldDecorator("id_department", {
                  //initialValue: 0,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    //mode="multiple"
                    loading={this.props.employee.loading}
                    showSearch
                    optionFilterProp="children"
                    //disabled={used ? true : false}
                  >
                    {permissions.includes("Create Department") ? (
                      <Option
                        key={"Create Department"}
                        value={undefined}
                        onMouseDown={e => e.preventDefault()}
                        onClick={this.props.handleModalAddDepartment}
                      >
                        <div style={{ color: "#1890FF", fontStyle: "italic" }}>
                          {" + " +
                            getLanguage({
                              id: "Department"
                            })}
                        </div>
                      </Option>
                    ) : null}
                    {this.props.employee.departments.map((data, key) => (
                      <Option key={key} value={data.id_department}>
                        {data.department_name}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="startDate" />}
              >
                {getFieldDecorator("start_date", {
                  //initialValue: moment()
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <DatePicker
                    style={{ width: "100%" }}
                    allowClear={false}
                    //disabled={used ? true : false}
                    // showTime={{ format: "HH:mm:ss" }}
                    format="DD MMMM YYYY"
                    //disabledDate={e => e.valueOf() > moment()}
                  />
                )}
              </Form.Item>
            </div>
            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 0 0 15px" }}
            >
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="gender" />}
              >
                {getFieldDecorator("gender", {
                  //initialValue: data.gender,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Radio.Group buttonStyle="solid" style={radioGroupStyle}>
                    <Radio.Button style={radioButtonStyle} value={1}>
                      <IntlMessages id="Male" />
                    </Radio.Button>
                    <Radio.Button style={radioButtonStyle} value={2}>
                      <IntlMessages id="Female" />
                    </Radio.Button>
                  </Radio.Group>
                )}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="employees.filter.nationality" />}
              >
                {getFieldDecorator("id_nationality", {
                  initialValue: this.props.employee.default_id_country,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={this.props.employee.loading}
                    showSearch
                    optionFilterProp="children"
                    //disabled={used ? true : false}
                  >
                    {this.props.employee.countries.map((data, key) => (
                      <Option key={key} value={data.id_country}>
                        {data.country_name}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="birthDate" />}
              >
                {getFieldDecorator("birth_date", {
                  //initialValue: moment()
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <DatePicker
                    style={{ width: "100%" }}
                    allowClear={false}
                    //disabled={used ? true : false}
                    // showTime={{ format: "HH:mm:ss" }}
                    format="DD MMMM YYYY"
                    //disabledDate={e => e.valueOf() > moment()}
                  />
                )}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="employees.filter.cityBirthPlace" />}
              >
                {getFieldDecorator("city_birthplace", {
                  //initialValue: 0,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Input
                    //   disabled={used ? true : false}
                    style={{ width: "100%" }}
                  />
                )}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="maritalStatus" />}
              >
                {getFieldDecorator("marital_status", {
                  //initialValue: data.gender,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Radio.Group
                    buttonStyle="solid"
                    style={{ display: "flex", width: "100%" }}
                  >
                    <Radio.Button style={radioButtonStyle} value={1}>
                      <IntlMessages id="single" />
                    </Radio.Button>
                    <Radio.Button style={radioButtonStyle} value={2}>
                      <IntlMessages id="married" />
                    </Radio.Button>
                    <Radio.Button style={radioButtonStyle} value={3}>
                      <IntlMessages id="divorced" />
                    </Radio.Button>
                  </Radio.Group>
                )}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="isTaxable" />}
              >
                {getFieldDecorator("is_taxable", {
                  //initialValue: data.gender,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Radio.Group
                    style={radioGroupStyle}
                    buttonStyle="solid"
                    onChange={this.handleChangeTaxable}
                  >
                    <Radio.Button style={radioButtonStyle} value={1}>
                      <IntlMessages id="yes" />
                    </Radio.Button>
                    <Radio.Button style={radioButtonStyle} value={0}>
                      <IntlMessages id="no" />
                    </Radio.Button>
                  </Radio.Group>
                )}
              </Form.Item>

              {this.state.is_taxable === 1 ? (
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="taxNumber" />}
                >
                  {getFieldDecorator("tax_number", {
                    //initialValue: 0,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Input
                      //   disabled={used ? true : false}
                      style={{ width: "100%" }}
                    />
                  )}
                </Form.Item>
              ) : null}
            </div>
          </div>

          <Row>
            <Col>
              <Form.Item {...tailFormItemLayout}>
                <Button
                  type="primary"
                  htmlType="submit"
                  style={{ marginTop: "20px", marginLeft: "50px" }}
                  loading={loading || loadingSubmit}
                >
                  <IntlMessages id="button.submit" />
                </Button>
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </>
    );
  }
}

const WrappedEmployeeCreateProfilForm = Form.create({
  name: "create_employee_profil"
})(EmployeeCreateProfilForm);

export default WrappedEmployeeCreateProfilForm;
