import React, { Component } from "react";

//import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
// import Button from "../../../../components/uielements/button";
import getLanguage from "../../../App/MyLanguange";
import { Col, Form, Input, Modal, Row } from "antd";

class ModalAddDepartment extends Component {
  state = {
    check_code: false
  };

  handleChangeCode = () => {
    const { setFieldsValue } = this.props.form;
    this.setState({ check_code: !this.state.check_code });
    if (this.state.check_code === false) {
      setFieldsValue({
        ["job_title_code"]: null
      });
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const department = this.props.department ? this.props.department : [];
    const { loadingSubmit } = department;

    // const used = data.is_used;
    const error = department.errorField ? department.errorField : [];

    const formAssetLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="departments.add.subtitle.data" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="40%"
        okText={getLanguage({ id: "button.submit" })}
        cancelText={getLanguage({ id: "button.cancel" })}
        okButtonProps={{ loading: loadingSubmit }}
        cancelButtonProps={{ loading: loadingSubmit }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          {/* {used ? (
          <Row style={{ marginBottom: "20px" }}>
            <Col span={24}>
              <Alert
                message={<IntlMessages id="departments.help" />}
                type="warning"
                showIcon
              />
            </Col>
          </Row>
        ) : null} */}

          <Row>
            <Col span={24}>
              <Form.Item
                {...formAssetLayout}
                label={<IntlMessages id="departments.code" />}
                validateStatus={
                  error.department_code || getFieldError("department_code")
                    ? "error"
                    : null
                }
                help={
                  error.department_code
                    ? error.department_code
                    : getFieldError("department_code")
                }
              >
                {getFieldDecorator("department_code", {
                  //initialValue: newDepartment ? null : data.department_code,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                    {
                      max: 30,
                      message: <IntlMessages id="maxLength30" />
                    }
                  ]
                })(
                  <Input
                  // addonBefore={prefix}
                  // disabled={used ? true : false}
                  />
                )}
              </Form.Item>

              <Form.Item
                {...formAssetLayout}
                label={<IntlMessages id="departments.name" />}
              >
                {getFieldDecorator("department_name", {
                  //  initialValue: data.department_name,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                    {
                      max: 50,
                      message: <IntlMessages id="maxLength50" />
                    }
                  ]
                })(
                  <Input
                  // disabled={used ? true : false}
                  />
                )}
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Modal>
    );
  }
}

const WrappedModalAddDepartment = Form.create({ name: "modal_add_department" })(
  ModalAddDepartment
);

export default WrappedModalAddDepartment;
