import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import EmployeeCreateProfileForm from "./createProfileForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/employee/employee/actions";
import jobTitleActions from "../../../../redux/employee/jobTitle/actions";
import departmentActions from "../../../../redux/companyMaster/department/actions";
import { Box } from "../../../App/app.style";
import { Tabs } from "antd";

const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/setup/employees";
const { clearSuccessJobTitle, storeJobTitle } = jobTitleActions;
const { clearSuccessDepartment, storeDepartment } = departmentActions;

class CreateEmployee extends Component {
  state = {
    visibleAddDepartment: false,
    visibleAddJobTitle: false
  };
  // Get All data diawal
  componentDidMount() {
    const { createEmployee } = this.props;
    createEmployee();
  }

  handleModalAddDepartment = () => {
    this.setState({
      visibleAddDepartment: !this.state.visibleAddDepartment
    });
  };

  handleModalAddJobTitle = () => {
    this.setState({
      visibleAddJobTitle: !this.state.visibleAddJobTitle
    });
  };

  handleSubmit = value => {
    const { storeEmployee } = this.props;
    value.start_date = value.start_date.format("YYYY-MM-DD");
    value.birth_date = value.birth_date.format("YYYY-MM-DD");
    //value.discount = value.discount/100;
    storeEmployee(value);
  };

  handleSubmitDepartment = value => {
    const { storeDepartment } = this.props;
    storeDepartment(value);
  };

  handleSubmitJobTitle = value => {
    const { storeJobTitle } = this.props;
    storeJobTitle(value);
  };

  componentDidUpdate() {
    // Display error notif
    if (this.props.employee.error) {
      notification("error", this.props.employee.error);
      this.props.clearError();
    }

    if (this.props.employee.success) {
      this.props.history.replace(
        redirectTo + "/update/" + this.props.employee.id_employee
      );
    }

    // job title ----------------------------------------------
    // Display error notif
    if (this.props.jobTitle.error) {
      notification("error", this.props.jobTitle.error);
      this.props.clearError();
    }

    if (this.props.jobTitle.success) {
      const { createEmployee, clearSuccessJobTitle } = this.props;
      notification("success", this.props.jobTitle.success);
      clearSuccessJobTitle();
      this.handleModalAddJobTitle();
      createEmployee();
    }

    // department ----------------------------------------------
    // Display error notif
    if (this.props.department.error) {
      notification("error", this.props.department.error);
      this.props.clearError();
    }

    if (this.props.department.success) {
      const { createEmployee, clearSuccessDepartment } = this.props;
      notification("success", this.props.department.success);
      clearSuccessDepartment();
      this.handleModalAddDepartment();
      createEmployee();
    }
  }

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="employees.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="employees.tab.employee" />} key="1">
              <EmployeeCreateProfileForm
                auth={this.props.auth}
                department={this.props.department}
                employee={this.props.employee}
                handleModalAddJobTitle={this.handleModalAddJobTitle}
                handleModalAddDepartment={this.handleModalAddDepartment}
                jobTitle={this.props.jobTitle}
                submit={this.handleSubmit}
                submitJobTitle={values => this.handleSubmitJobTitle(values)}
                submitDepartment={values => this.handleSubmitDepartment(values)}
                visibleAddJobTitle={this.state.visibleAddJobTitle}
                visibleAddDepartment={this.state.visibleAddDepartment}
              />
            </TabPane>
            {/* <TabPane tab={<IntlMessages id="employees.tab.contact" />} 
              key="2"
              disabled
            >  
            </TabPane>
            <TabPane tab={<IntlMessages id="employees.tab.address" />} 
              key="3"
              disabled
            > 
            </TabPane>
            <TabPane tab={<IntlMessages id="employees.tab.username" />} 
              key="4"
              disabled
            > 
            </TabPane> */}
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    department: state.department,
    employee: state.employee,
    jobTitle: state.jobTitle
  };
};

export default compose(
  connect(mapStateToProps, {
    ...actions,
    clearSuccessDepartment,
    clearSuccessJobTitle,
    storeDepartment,
    storeJobTitle
  })
)(CreateEmployee);
