import React, { Component } from "react";

//import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
// import Button from "../../../../components/uielements/button";
import getLanguage from "../../../App/MyLanguange";
import { Form, Input, Col, Checkbox, Modal } from "antd";

// const { TextArea } = Input;
const InputGroup = Input.Group;

class ModalAddJobTitle extends Component {
  state = {
    check_code: false
  };

  handleChangeCode = () => {
    const { setFieldsValue } = this.props.form;
    this.setState({ check_code: !this.state.check_code });
    if (this.state.check_code === false) {
      setFieldsValue({
        ["job_title_code"]: null
      });
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { jobTitle } = this.props;
    const { loading } = jobTitle ? jobTitle : [];
    // const data = jobTitle.jobTitle ? jobTitle.jobTitle : [];

    const error = jobTitle.errorField ? jobTitle.errorField : [];
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="jobTitles.button.add" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="40%"
        okText={getLanguage({ id: "button.submit" })}
        cancelText={getLanguage({ id: "button.cancel" })}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="jobTitles.add.subtitle.data" />
        </Divider> */}
          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0" }}
            >
              <InputGroup compact>
                <Col span={20}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="jobTitles.add.code" />}
                    validateStatus={
                      error.job_title_code || getFieldError("job_title_code")
                        ? "error"
                        : null
                    }
                    help={
                      error.job_title_code
                        ? error.job_title_code
                        : getFieldError("job_title_code")
                    }
                  >
                    {getFieldDecorator("job_title_code", {
                      // initialValue: data.job_title_code,
                      rules: [
                        {
                          max: 30,
                          message: <IntlMessages id="maxLength30" />
                        }
                      ]
                    })(
                      <Input disabled={this.state.check_code ? true : false} />
                    )}
                  </Form.Item>
                </Col>
                <Col span={4}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="autoGenerateCode" />}
                  >
                    <Checkbox
                      style={{
                        paddingTop: "5px",
                        paddingLeft: "8px"
                      }}
                      checked={this.state.check_code}
                      onChange={this.handleChangeCode}
                    />
                  </Form.Item>
                </Col>
              </InputGroup>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="jobTitles.add.name" />}
              >
                {getFieldDecorator("job_title_name", {
                  // initialValue: data.job_title_name,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                    {
                      max: 50,
                      message: <IntlMessages id="maxLength50" />
                    }
                  ]
                })(
                  <Input
                  //   disabled={used ? true : false}
                  />
                )}
              </Form.Item>
            </div>
          </div>
        </Form>
      </Modal>
    );
  }
}

const WrappedModalAddJobTitle = Form.create({ name: "modal_add_job_title" })(
  ModalAddJobTitle
);

export default WrappedModalAddJobTitle;
